ks-product-brand {
    display: flex;
    justify-content: flex-end;

    position: absolute;
	bottom: 0;
    right: 0;
    left: 0;
}

ks-product-brand > a {
    display: block;

    max-width: 150px;
    height: 60px;

    transition: opacity 0.3s ease,
                transform 0.3s ease;
}

ks-product-brand > a:hover {
    opacity: 0.8;
}

ks-product-brand > a:active {
    opacity: 0.5;
    transform: scale(0.95);
}

ks-product-brand img {
    width: unset !important;
}

@media(max-width: 960px) {
    ks-product-brand {
        justify-content: center;
    }
}