ks-product-variants {
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;

    box-sizing: border-box;
    width: auto;
}

@media (min-width: 1200px) {
    ks-product-variants {
        margin: -9px -9px 9px -9px !important;
        width: calc(100% + 18px);
    }
}

ks-product-variants ks-product-variant {
    width: 10%;
}

ks-product-variants .more {display: none}

ks-product-variants[toggle] ks-product-variant:nth-child(n+11) { display: none }
ks-product-variant:nth-child(n+11) ~ .more { display: block }

@media (max-width: 1100px) {
    ks-product-variants ks-product-variant { width: 11.11111111111111%; }
    ks-product-variants[toggle] ks-product-variant:nth-child(n+10) { display: none }
    ks-product-variant:nth-child(n+10) ~ .more {display: block}
}
@media (max-width: 1000px) {
    ks-product-variants ks-product-variant { width: 12.5%; }
    ks-product-variants[toggle] ks-product-variant:nth-child(n+9) { display: none }
    ks-product-variant:nth-child(n+9) ~ .more {display: block}
}
