    ks-product-variants ks-product-variant { width: 16.66666666666667%; }
    ks-product-variants[toggle] ks-product-variant:nth-child(n+7) { display: none }
    ks-product-variant:nth-child(n+7) ~ .more {display: block}
}
@media (max-width: 600px) {
    ks-product-variants ks-product-variant { width: 20%; }
    ks-product-variants[toggle] ks-product-variant:nth-child(n+6) { display: none }
    ks-product-variant:nth-child(n+6) ~ .more {display: block}
}
@media (max-width: 500px) {
    ks-product-variants ks-product-variant { width: 25%; }
    ks-product-variants[toggle] ks-product-variant:nth-child(n+5) { display: none }
    ks-product-variant:nth-child(n+5) ~ .more {display: block}
}
@media (max-width: 400px) {
    ks-product-variants ks-product-variant { width: 33.33333333333333%; }
    ks-product-variants[toggle] ks-product-variant:nth-child(n+4) { display: none }
    ks-product-variant:nth-child(n+4) ~ .more {display: block}
