@media (max-width: 300px) {
    ks-product-variants ks-product-variant { width: 50%; }
    ks-product-variants[toggle] ks-product-variant:nth-child(n+3) { display: none }
    ks-product-variant:nth-child(n+3) ~ .more {display: block}
