ks-product-variants ks-product-variant {
    padding: 9px;
    box-sizing: border-box;
}

ks-product-variants[toggle] ks-product-variant:nth-child(n+12) {
    display: none;
}
ks-product-variants {
    flex: 1;
}

ks-product-variants .more {
    flex: 1 0 100%;
}

ks-product-variants ks-button {
    margin: 15px auto 0px auto;
}
