import { Component, h, Prop, Element } from '@stencil/core';

@Component({
    tag: 'ks-product-tabs',
    styleUrl: 'product-tabs.css'
})
export class ProductTabs {
    @Element() root: HTMLElement;
    @Prop() names: string =  "";
    @Prop({mutable: true, reflect: true}) active: number = 0;

	render() {
		return [
            <nav role="tablist" aria-label="Opisy produktu"> 
                {this.names.split(", ").map((value, index)=>
                    <button class={index == this.active ? "active" : ""} role="tab" aria-selected={index === this.active ? "true" : "false"}
                            onClick={()=>this.click(index)}>
                        {value}
                    </button>
                )} 
            </nav>,

            <div class="content" role="tabpanel">
                <slot />
            </div>
        ];
    }

    click(index: number) {
        this.active = index;
        Array.from(this.root.querySelector(".content").children).forEach(
            (element, i) => {
                const target = (element as any);
                target.main = i == index;
                target.open = i == index;
            }
        );
    }
}
