ks-product-notify-edrone {
    display: block;
}

ks-product-notify-edrone fieldset {
    border: none;
    margin: 0;
    padding: 0;
}

ks-product-notify-edrone .info {
    text-align: center;
    margin-bottom: 40px;
}

ks-product-notify-edrone .info a {
    text-decoration: none;
    color: black;
    margin: 0 5px;
}

ks-product-notify-edrone h3 {
    font-family: var(--font-emphasis);
    font-size: 20px;
    font-weight: 700;
}

ks-product-notify-edrone ks-input-check {
    margin-bottom: 5px;
}

ks-product-notify-edrone ks-button {
    margin-top: 20px;
}