import { Component, h, Prop, Element, } from '@stencil/core';
import { TabDataGSPR, TabDataGSPRInfo } from '../../global/data/product';

const regionNames = new Intl.DisplayNames(['pl'], {type: 'region'});

@Component({
    tag: 'ks-product-tab',
    styleUrl: 'product-tab.css'
})
export class ProductTab {
    @Element() root: HTMLKsProductTabElement;

    @Prop({mutable: true, reflect: true}) main: boolean;
    @Prop({mutable: true, reflect: true}) open: boolean;
    @Prop() name: string;
    @Prop() content: string;
    @Prop() gspr: TabDataGSPR;


    renderContactInfo(contact: TabDataGSPRInfo) {
        return <div>
            {contact.name && <p>{contact.name}</p>}
            {contact.street && <p>{contact.street}</p>}
            {(contact.city || contact.postal_code) && <p>
                {contact.city} {contact.postal_code}
            </p>}
            {contact.country && <p>
                {regionNames.of(contact.country)}
            </p>}
            
            <br/>
            {contact.email && <p>{contact.email}</p>}
            {contact.website && <p>{contact.website}</p>}
            {contact.phone && <p>{contact.phone}</p>}
        </div>
    }

	render() {
        return [
			<button class="accordion" onClick={()=>this.onOpen()}>
                {this.name}
                <ks-icon name={this.open ? "minus" : "plus"}></ks-icon>
            </button>,
            <div class="tab-content" innerHTML={this.content}>
                {this.gspr && <div class="gspr">
                    {this.gspr.manufacturer && <div>
                        <h3>Informacje kontaktowe producenta</h3>
                        {this.renderContactInfo(this.gspr.manufacturer)}
                    </div>}

                    {this.gspr.responsible && <div>
                        <h3>Informacje kontaktowe podmiotu odpowiedzialnego</h3>
                        {this.renderContactInfo(this.gspr.responsible)}
                    </div>}

                    {!this.gspr.manufacturer && !this.gspr.responsible && this.gspr.name && this.gspr.info && <div>
                        <h3>Informacje kontaktowe producenta</h3>
                        {this.gspr.name && <p>{this.gspr?.name}</p>}
                        {this.gspr.info && <p innerHTML={this.gspr.info.replace(/(?:\r\n|\r|\n)/g, '<br>')} />}
                    </div>}
                </div>}
            </div>
        ];
    }

    onOpen() {
        this.open = !this.open;
        if(this.open) {
            const tabs = Array.from(this.root.parentElement.children) as Array<HTMLKsProductTabElement>;
            const index = tabs.indexOf(this.root);

            tabs.forEach(element => element.removeAttribute("main"));
            this.main = true;

            (this.root.closest('ks-product-tabs') as any).active = index;
        }
    }
    
    
}
