import { Component, h, Element, Prop } from '@stencil/core';

@Component({
    tag: 'ks-product-image-popup',
    styleUrl: 'product-image-popup.css'
})
export class ProductImagePopup {
    @Element() root: HTMLElement;
    @Prop() image: string;

    showLightbox() {
        this.root.querySelector("ks-overlay").show()
    }

	render() {
		return[
            <button onClick={()=>this.showLightbox()}>
                <slot />
            </button>,
            <ks-overlay dark>
                <div class="content">
                    <ks-img3 image={this.image} fit='contain' />
                    <div class="close" onClick={()=>this.root.querySelector("ks-overlay").hide()}>
                    <ks-icon name="x" size={1.2}></ks-icon>
                    </div>
                </div>
            </ks-overlay>
		];
	}
}
