import { Component, h, Element } from '@stencil/core';
import { product } from "../../../global/data/product";
import { commonDynamic } from "../../../global/data/commonDynamic";
import ValidateInput from '../../input/validate';

@Component({
    tag: 'ks-product-negotiate',
    styleUrl: 'product-negotiate.css'
})
export class ProductNegotiate {
    @Element() root: HTMLElement;
    dialog: HTMLKsDialogElement;

    async requestHandler(event: Event) {
        const strings = product.get('strings');

        event.preventDefault();
        if(!await ValidateInput(this.root.querySelector('form')))
            return;

        this.dialog.showLoading();

        const target = event.target as HTMLFormElement;
        const data = new FormData(target);
        data.append("productUrl", document.location.href);
        data.append("productPrice", product.get('currentPrice').toString());
        data.append("productOldPrice", product.get('previousPrice').toString());
        data.append("productName", product.get('name'));

        await fetch(commonDynamic.get('api').negotiatePrice, { body: data, method: "post"})
        .then(async response => {
            const result = await response.text();
            if(result == "success")
                this.dialog.showSuccess(strings.negotiateSuccessHeading, strings.negotiateSuccessMessage);
            else this.dialog.showFailure(strings.negotiateFaliureHeading, strings.negotiateFaliureMessage);
        })
        .catch(async error => {
            let message = "";

            if(!window.navigator.onLine)
                message = strings.noInternet

            if(error.message) message = error.message;

            this.dialog.showFailure(strings.negotiateFaliureHeading, message);
        });
    }

    componentDidRender() {
        this.dialog = this.root.querySelector("ks-dialog");
        const button = this.root.parentElement.querySelector("ks-product-negotiate > *:first-child");
        button.addEventListener("click", ()=>{
            this.dialog.show();
        });
    }

	render() {
        const strings = product.get('strings');

		return [
            <slot/>,
            <ks-dialog dark>
                <form onSubmit={e => this.requestHandler(e)}>
                    <fieldset>
                        <div class="info">
                            <h3>{strings.negotiateHeading}</h3>
                            <p>{strings.negotiateParagraph}</p>
                        </div>

                        <ks-input-text name="name" required nomessage placeholder={strings.negotiateName} icon="user"/>
                        <ks-input-text email name="email" required nomessage placeholder={strings.negotiateEmail} icon="mail"/>
                        <ks-input-text url name="url" required nomessage placeholder={strings.negotiateLink} icon="link"/>
                        <ks-input-textarea rows={4} name="comment" placeholder={strings.negotiateComment} noresize></ks-input-textarea>
                        <ks-input-check name="zgoda" required nomessage label={strings.negotiateAgreement}></ks-input-check>

                        <ks-button submit name={strings.negotiateSubmit} />
                    </fieldset>
                </form>
            </ks-dialog>
        ];
    }

}
