ks-product-message {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    padding: 15px 20px;
    border-radius: 15px;

    background-color: var(--color-secondary-darker);
    color: var(--text-color-secondary);

    opacity: 1;
    animation: flash 1s ease infinite;
}

@media screen and (max-width: 1200px) {
    ks-product-message {
        border-radius: 0px;
        margin: 0;
    }
}

@keyframes flash {
    0% { opacity: 1; }
    33% { opacity: 1; }
    66% { opacity: 0.7; }
    100% { opacity: 1; }
}