ks-product-youtube {
    display: block;

    position: relative;
    overflow: hidden;
    box-sizing: border-box;

    padding-top: 56.25%;

    background-color: black;
    border-radius: 10px;
}

ks-product-youtube iframe {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
}

@media (max-width: 1200px) {
    ks-product-youtube {
        border-radius: 0px;
    }
}