ks-product-tabs {
    display: block;
    position: relative;
    box-sizing: border-box;

    padding: 0;

    background: var(--card-background);
    color: var(--card-text-color);
    border-radius: 10px;
    overflow: hidden;
}

ks-product-tabs nav {
    background-color: #f6f6f6;
    border: 1px solid #ffffff;
    border-bottom: none;
    display: flex;
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

@media only screen and (max-width: 960px) {
    ks-product-tabs nav {
        display: none;
    }
}

ks-product-tabs nav button {
    position: relative;
    padding: 18px 30px;
    margin: 0;

    font-family: inherit;
    font-size: 17px;

    color: #252525;
    background-color: #f6f6f6;
    transition: background-color 0.3s ease, color 0.3s ease;
    
    border: none;

    border-right: 1px solid #e8e8e8;
}

ks-product-tabs nav button.active {
    color: #252525 !important;
    background-color: #FFFFFF;
}

ks-product-tabs nav button:hover {
    color: #808080;
}


ks-product-tabs > .content {
    padding: 30px;
}

@media only screen and (max-width: 520px) {
    ks-product-tabs > .content {
        padding: 30px 20px;
    }
}