import { Component, h, Host } from '@stencil/core';
import { product } from "../../../global/data/product";

@Component({
	tag: 'ks-product-message',
	styleUrl: 'product-message.css'
})
export class ProductMessage {
	render() {
		if(!product.get("message")) return;

		return <Host>
			{product.get("message") ?? null}
		</Host>;
	}
}