import { Component, h, Element, Event, EventEmitter } from '@stencil/core';
import { product, TraitData, TraitDataItem } from "../../../global/data/product";

@Component({
	tag: 'ks-product-traits',
	styleUrl: 'product-traits.css'
})
export class ProductCount {
	@Element() root: HTMLElement;
	
	@Event() traitChange: EventEmitter<[TraitData, TraitDataItem][]>;
    traitChangeHandler() {
		if(!product?.get("traits"))	return;

		const selects = this.root.querySelectorAll(".trait select");

		const data = product.get("traits").map((trait: TraitData, index: number) : [TraitData, TraitDataItem] => {
			const select = selects[index] as HTMLSelectElement;
			return [trait, trait.items[select.selectedIndex]];
		});

        this.traitChange.emit(data);
	}

	render() {
		if(!product?.get("traits")) return null;

		return product.get("traits").map((trait: TraitData) => 
			<div class="trait">
				<label htmlFor={trait.name}>{trait.name}:</label>
				<div class="select">
					<select id={trait.name} onChange={()=>this.traitChangeHandler()}>
						{trait.items.map(item =>
							<option value={item.id}>{item.name}</option>
						)}
					</select>
					<ks-icon name="chevron-down"></ks-icon>
				</div>
			</div>
		);
	}
}
