ks-product-image-popup  {
    display: block;
}

ks-product-image-popup > button  {
    background-color: transparent;
    border: none;
    cursor: pointer;
}

ks-product-image-popup .content  {
    display: flex;
    flex-flow: column nowrap;

    padding: 10px;
    max-width: 95vw;
    max-height: 95vh;
    box-sizing: border-box;
    user-select: none;
}

ks-product-image-popup .close {
    position: absolute;
    top: 15px;
    right: 15px;

    backdrop-filter: var(--lightbox-element-filter);
    background-color: var(--lightbox-element-color);
    border-radius: 100%;
    padding: 5px;

    cursor: pointer;
    user-select: none;

    z-index: 1000000;
    transition: var(--transition-background-color),
                transform 0.1s ease;
}

ks-product-image-popup .close:hover {
    background-color: var(--lightbox-element-color-hover);
    transform: scale(1.05, 1.05);
}

ks-product-image-popup .close:active {
    background-color: var(--lightbox-element-color-active);
    transform: scale(0.95, 0.95);
}

@media only screen and (max-width: 720px) {
    ks-product-image-popup .content {
        padding: 0;
    }
}