import { Component, h, Element, Prop, Listen } from '@stencil/core';

@Component({
	tag: 'ks-product-installments',
	styleUrl: 'product-installments.css'
})
export class ProductInfo {
	@Element() root: HTMLElement;
    @Prop() name: string = "";
    @Prop({reflect: true, mutable: true}) active: boolean = false;

    @Listen("click", {target: "window"})
    clickOutside(event: Event) {
        const target = event.target as HTMLElement;

        if(!target.closest('ks-product-installments'))
            this.active = false;

        return target;
    }

	render() {
		return [
			<ks-product-button onClick={()=> this.active = !this.active}>
                {this.name}
            </ks-product-button>,
            <div class="arrow" />,
            <div class="content"><slot/></div>
		]
	}
}
