import { Component, h } from '@stencil/core';
import { product } from "../../../global/data/product";

@Component({
	tag: 'ks-product-brand',
	styleUrl: 'product-brand.css'
})
export class ProductBrand {
	render() {
        const brand = product.get("brand");
		
		if(!brand) return null;

		return <a href={brand.link} aria-label={`Producent: ${brand}`}>
            <ks-img2 src={brand.logo} alt={brand.name} width={brand.width} height={brand.height}/>
        </a>;
	}
}