import { Component, h, Element, Prop } from '@stencil/core';
import { product } from '../../../global/data/product';

@Component({
	tag: 'ks-product-variants',
	styleUrl: 'product-variants.css'
})
export class ProductVariants {

	@Element() root: HTMLElement;
	@Prop({mutable: true, reflect: true}) toggle: boolean = true;

	render() {
		return [
			...product.get("variants").map(variant => 
            	<ks-product-variant link={variant.link} image={variant.image} webp={variant.webp} active={variant.active} unavailable={variant.unavailable} name={variant.name} />
			),

			<div class="more">
				<ks-button transparent narrower round icon={this.toggle ? "plus" : "minus"} ariaLabel='Pokaż więcej wariantów'
					onClick={()=>this.toggle = !this.toggle}
				/>
			</div>
		];
	}
}
