ks-product-shipping {
    display: block;
    width: 100%;

    margin-top: 10px;
}

ks-product-shipping .message {
    font-family: var(--product-shipping-message-font);
    font-size: 15px;
    color: var(--color-secondary);
    margin-bottom: 15px;
    border-radius: 20px;
    font-weight: 700;

    max-width: 100%;
}


ks-product-shipping .hidden {
    display: none;
}

@media only screen and (max-width: 960px) {
    ks-product-shipping {
        display: flex;
        text-align: center;
        flex-direction: column;
        align-items: center;
    }
}