import { Component, h, State, Element, Event, EventEmitter, Prop } from '@stencil/core';
import { product } from "../../../global/data/product";

@Component({
	tag: 'ks-product-purchase',
	styleUrl: 'product-purchase.css'
})
export class ProductPurchase {
    @Element() root: HTMLElement;
    @Prop() cartText = "Do koszyka";
    @Prop() availabilityText = "Powiadom o dostępności";
    @Prop() favouritesIcon = "star";
    
    @Event() addToCart: EventEmitter;
    CartHandler() {
        if(product.get("availability") > 0) {
            if(!product.get("cartLoading"))
                this.addToCart.emit();
        }
        else document.querySelector("ks-product-notify-edrone").show();
    }

    @Event() addToFavourites: EventEmitter;
    FavouritesHandler() {
        if(!product.get("favouritesLoading") && !product.get("favouritesCompleted")) {
            this.addToFavourites.emit();
        }
    }

    @State() cartAnimation = false;
    @State() favouritesAnimation = false;

	render() {
        const available = product.get("availability") > 0;

        if(product.get("cartLoading"))
            this.cartAnimation = true;
        else setTimeout(() => {
            this.cartAnimation = false;
        }, 300);

        if(product.get("favouritesLoading"))
            this.favouritesAnimation = true;
        else setTimeout(() => {
            this.favouritesAnimation = false;
        }, 300);

        const favClass = [
            "fav",
            product.get("favouritesCompleted") ? "completed" : null,
            product.get("favouritesLoading") ? "loading" : null
        ];

		return [
            <button onClick={()=>this.CartHandler()}
                class={"cart" + (product.get("cartLoading") ? " loading" : "") + (available ? "" : " disabled")}>

                {available ? this.cartText : this.availabilityText}
                <ks-loader oversized running={this.cartAnimation}></ks-loader>
            </button>,

            available ? <ks-product-count></ks-product-count> : null,
            
            <button onClick={()=>this.FavouritesHandler()} aria-label="Dodaj do ulubionych"
                 class={favClass.join(" ")}>
                <ks-icon name={this.favouritesIcon} aria-hidden></ks-icon>
                <ks-loader running={this.favouritesAnimation}></ks-loader>
                <ks-icon class="completed" name="check" aria-hidden></ks-icon>
            </button>
		]
	}
}
