import { Component, h, Host } from '@stencil/core';
import { product } from '../../../global/data/product';

@Component({
	tag: 'ks-review-product',
    styleUrl: 'review-product.css'
})
export class ReviewProduct {

	render() {
        const reviews = product.get('reviewProducts');
        const count = reviews.length;
        const average = parseFloat((reviews.reduce((previous, current) => previous + current.rating, 0) / count).toFixed(2));

        const positive = parseFloat((reviews.filter((review) => review.rating >= 4).length / count * 100).toFixed(2));
        const neutral = parseFloat((reviews.filter((review) => review.rating < 4 && review.rating > 2).length / count * 100).toFixed(2));
        const negative = parseFloat((reviews.filter((review) => review.rating <= 2).length / count * 100).toFixed(2));

        const strings = product.get("strings");

		return <Host>
            <div class="summary">
                <div class="count">
                    <div class="message">{strings.reviewsCount}</div>
                    <div class="content">{count}</div>
                </div>
                <div class="average">
                    <div class="message">{strings.reviewsAverage}</div>
                    <div class="content">{average}/5</div>
                </div>
                <div class="positivity">
                    <div class="bar">
                        <span>{strings.reviewsPositive}</span>
                        <div class="right">
                            <span>{positive}%</span>
                            <div class="progress"><span style={{"width": `${positive}%`}}></span></div>
                        </div>
                    </div>
                    <div class="bar">
                        <span>{strings.reviewsNeutral}</span>
                        <div class="right">
                            <span>{neutral}%</span>
                            <div class="progress"><span style={{"width": `${neutral}%`}}></span></div>
                        </div>
                    </div>
                    <div class="bar">
                        <span>{strings.reviewsNegative}</span>
                        <div class="right">
                            <span>{negative}%</span>
                            <div class="progress"><span style={{"width": `${negative}%`}}></span></div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="verification">
                Opinie są weryfikowane pod kątem dokonania zakupu i otrzymania produktu.
            </div>
            <div class="reviews">
                {reviews.map(review =>
                    <div class="review" aria-label={`${review.rating} z 5 - ${review.review} - ${review.date}`}>
                        <div class="top">
                            <div class="stars">{this.stars(review.rating)}</div>
                            <div class="date">{review.date}</div>
                        </div>
                        <div class="content">{review.review}</div>
                    </div>    
                )}
            </div>
        </Host>;
	}

    stars(rating) {
        return [...Array(5).keys()].map(index =>
            this.star(Math.round(rating) >= index+1)
        );
    }

    star(on) {
        return <svg class={on ? "" : "off"} width="100%" height="100%" viewBox="0 0 50 50" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlSpace="preserve" style={{fillRule: "evenodd", clipRule: "evenodd", strokeLinejoin: "round", strokeMiterlimit: "2"}}>
            <g transform="matrix(1,0,0,1,0,2.38729)">
                <path d="M25,0L30.613,17.275L48.776,17.275L34.082,27.951L39.695,45.225L25,34.549L10.305,45.225L15.918,27.951L1.224,17.275L19.387,17.275L25,0Z" style={{fill:"rgb(255 195 0)"}}/>
            </g>
        </svg>;
    }
}
