import { Component, h, Element, Method } from '@stencil/core';
import { product } from "../../global/data/product";
import { commonDynamic } from "../../global/data/commonDynamic";
import ValidateInput from '../input/validate';

@Component({
    tag: 'ks-product-notify-edrone',
    styleUrl: 'product-notify-edrone.css'
})
export class ProductNotifyEdrone {
    @Element() root: HTMLElement;
    dialog: HTMLKsDialogElement;

    async requestHandler(event: Event) {
        const strings = product.get("strings");
        const productId = product.get("id");
        const api = commonDynamic.get('tracking').edroneWishlistApi;

        event.preventDefault();
        if(!await ValidateInput(this.root.querySelector('form')))
            return;

        this.dialog.showLoading();

        const target = event.target as HTMLFormElement;
        const data = new FormData(target);
        data.append("app_id", commonDynamic.get('tracking').edroneAppId);
        data.append("topic_id", productId);
        data.append("topic_campaign_id", '0');

        await fetch(api, { body: data, method: "post"})
        .then(() => {
            this.dialog.showSuccess(strings.notifySuccessHeading, strings.notifySuccessParagraph);
        })
        .catch(async error => {
            let message = "";

            if(!window.navigator.onLine)
                message = strings.noInternet;

            else if(error.message != "")
                message = error.message;

            console.log(error.message);
            this.dialog.showFailure(strings.notifyErrorHeading, message);
        });
    }

    @Method()
    async show() {
        this.dialog.show();
    }

    componentDidLoad() {
        this.dialog = this.root.querySelector('ks-dialog');
    }

	render() {
        const strings = product.get("strings");

		return <ks-dialog dark>
            <form onSubmit={e => this.requestHandler(e)}>
                <fieldset>
                    <div class="info">
                        <h3>{strings.notifyHeading}</h3>
                        <p>{strings.notifyParagraph}</p>
                    </div>

                    <ks-input-text email name="email" required nomessage placeholder="E-mail" icon="mail"/>
                    <ks-input-check name="zgoda" required nomessage label={strings.notifyAgreement}></ks-input-check>

                    <ks-button submit name={strings.notifyMe} />
                </fieldset>
            </form>
        </ks-dialog>;
    }

}
