ks-review-product {
    display: block;
    position: relative;
    box-sizing: border-box;
    padding: 0;
    background: var(--card-background);
    color: var(--card-text-color);
    font-family: var(--font-emphasis);

    border-radius: 10px;
}


ks-review-product .summary {
    display: flex;
}

ks-review-product .summary > * {
    width: 70%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

ks-review-product .summary > *:not(:last-child) {
    border-right: #dddddd solid 1px;
}

ks-review-product .summary .positivity {
    width: 100%;
}

ks-review-product .summary .message {
    font-size: 16px;
}

ks-review-product .summary .content {
    font-size: 32px;
    font-weight: 700;
}

ks-review-product .summary .bar {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 300px;
}

ks-review-product .summary .right {
    display: flex;
    align-items: center;
    width: 60%;
}

ks-review-product .summary .bar .progress { 
    height: 10px;
    position: relative;
    background: #fafafa;
    border-radius: 5px;
    margin-left: 5px;
    width: 100%;
    overflow: hidden;
}

ks-review-product .summary .bar .progress > span {
    display: block;
    height: 100%;
    background-color: rgb(255 195 0);
    position: relative;
    overflow: hidden;
}


ks-review-product .reviews {
    border-top: #dddddd solid 1px;
}

ks-review-product .review {
    padding: 13px 18px;
}

ks-review-product .review:not(:last-child) {
    border-bottom: #dddddd solid 1px;
}

ks-review-product .review .top {
    display: flex;
}

ks-review-product .review .top .date {
    margin-left: 10px;
}

ks-review-product .stars {
    display: flex;
    max-width: 120px;
    margin-bottom: 10px;
}

ks-review-product .stars .off {
    opacity: 0.3;
}

ks-review-product .verification {
    border-top: #ddd 1px solid;
    padding: 10px 20px;
    text-align: center;
    font-size: 14px;
    background-color: #f2f2f2;
}

@media (max-width: 600px) {
    ks-review-product .summary {
        flex-wrap: wrap;
    }

    ks-review-product .summary .count,
    ks-review-product .summary .average {
        width: 50%;
        box-sizing: border-box;
        border-bottom: #dddddd solid 1px;
    }

    ks-review-product .summary .positivity {
        width: 100%;
    }
}