ks-product-admin {
    display: none;
}

ks-product-admin[enabled] {
    display: block;
}

ks-product-admin .bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    background-color: #181818;
    color: white;
}

ks-product-admin .set .bar {
    background-color: #353535;
}

ks-product-admin .bar > div {
    flex: 0 1 auto;
    white-space: nowrap;
}

ks-product-admin .bar > div:not(.links).name {
    flex: 1 1 auto;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
}

ks-product-admin .bar > div:not(.links) {
    padding: 17px;
}

ks-product-admin .bar > .links {
    display: flex;
    justify-content: flex-end;

    flex: 1;
}

ks-product-admin .bar > .links .button {
    display: block;
    padding: 17px;

    color: white;

    background-color: #252525;
    transition: background-color 0.3s ease;

    border-style: none;
    outline-style: none;
    user-select: none;
}

ks-product-admin .bar > .links .button:hover {
    color: white;
    background-color: #353535;
}

ks-product-admin .bar > .links .button:active {
    color: white;
    background-color: #454545;
}

ks-product-admin .bar > .links .button:nth-child(2n) {
    background-color: #303030;
}

ks-product-admin .bar > .links .button:nth-child(2n):hover {
    background-color: #404040;
}

ks-product-admin .bar > .links .button:nth-child(2n):active {
    background-color: #505050;
}


ks-product-admin .distributors {
    display: table;

    background-color: #000000;
    color: #ffffff;

    width: 100%;
    box-sizing: border-box;

    overflow: hidden;
}

ks-product-admin .distributors[hidden] {
    display: none !important;
    visibility: hidden !important;
}

ks-product-admin .distributors th {    
    font-weight: 700;
    font-size: 15.5px !important;
    font-family: var(--font-emphasis);

    border-bottom: 2px solid #181818;
    border-right: 2px solid #181818;
}

ks-product-admin .distributors td  {
    font-weight: 500;
    font-size: 14px;
    border-right: 2px solid #181818;
}

ks-product-admin .distributors th, ks-product-admin .distributors td  {
    text-align: center;
    padding: 8px 20px;
}

ks-product-admin .distributors th:first-child, ks-product-admin .distributors td:first-child  {
    text-align: left;
}

ks-product-admin .distributors *:last-child td  {
    padding: 8px 20px 14px 20px;
}

ks-product-admin td.overwrite {
    color: var(--color-secondary);
}


@media only screen and (max-width: 640px) and (min-width: 340px) {
    ks-product-admin .bar {
        font-size: 14px;
    }

    ks-product-admin .bar > div:not(.links) {
        padding: 17px 10px;
    }
}

@media only screen and (max-width: 420px) {
    ks-product-admin .bar {
        font-size: 12px;
    }

    ks-product-admin .bar > div:not(.links) {
        padding: 17px 8px;
    }

    ks-product-admin .bar .hide-mobile {
        display: none !important;
    }
}

@media only screen and (max-width: 960px) {
    ks-product-admin .bar > .links button {
        display: none !important;
    }
    
    ks-product-admin .distributors {
        display: none !important;
        visibility: hidden !important;
    }
}
