import { Component, h, Element, Prop, Listen, State } from '@stencil/core';

declare var OpenPayU: any;
@Component({
	tag: 'ks-product-calculator-payu'
})
export class ProductCalculatorPayU {
    @Element() root: HTMLElement;

    @Prop() price: number;
    @Prop() posId: string = "";
    @Prop() apiKey: string = "";

    @State() enabled: boolean = false;

    @Listen("click") clickHandler() {
        if(this.enabled) this.buttonHandler();
        else this.enabled = true;
    }

    loadHandler() {
        (window as any).openpayu = (window as any).openpayu || {};
        (window as any).openpayu.options = {
            creditAmount: this.price,
            posId: this.posId,
            key: this.apiKey,
            showLongDescription: true
        }

        OpenPayU.Installments.miniInstallment("#calculator-payu");
        this.buttonHandler();
    }

    buttonHandler() {
        let payuLink = document.querySelector("#calculator-payu a") as HTMLLinkElement;
        payuLink.click();
    }

	render() {
		return [
            <slot/>,

            this.enabled ? [
                <span id="calculator-payu" style={{display: "none"}}></span>,
                <script onLoad={()=>this.loadHandler()} src="https://static.payu.com/res/v2/widget-products-installments.min.js"></script>,
            ] : null
        ];
    }
}