ks-product-tab {
    display: block;
}

@media only screen and (min-width: 960px) {
    ks-product-tab .accordion {
        display: none;
    }
}

ks-product-tab > button {
    display: flex;
    justify-content: space-between;
    background-color: transparent;
    color: #151515;

    font-family: var(--font-emphasis);
    font-size: 18px;
    font-weight: 700;
    width: 100%;

    border: none;
    outline: none;
    padding: 10px 0px 20px 0px;
    margin: 0;

    transition: color 0.3s ease;
}

ks-product-tab > button:hover {
    color: #606060;
}


ks-product-tab:not([open]) > button > ks-icon {
    transform: rotate(0deg);
    transition: transform 0.3s ease;
}

ks-product-tab:not([open]) > button:hover > ks-icon {
    transform: rotate(90deg);
}

ks-product-tab > .tab-content {
    display: none;
    max-width: 100%;
}

ks-product-tab .file {
    max-width: 300px;
}

ks-product-tab .gspr {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
}

ks-product-tab .gspr > * {
    flex: 0 1 auto;
}

ks-product-tab .gspr p {
    margin-top: 0;
    margin-bottom: 5px;
}


@media only screen and (min-width: 960px) {
    ks-product-tab[main] > .tab-content {
        display: block;
    }
}

@media only screen and (max-width: 960px) {
    ks-product-tab[open] > .tab-content {
        display: block;
    }
}
