import { Component, h, Prop, Element } from '@stencil/core';
import { product } from "../../global/data/product";

@Component({
	tag: 'ks-product-tags',
	styleUrl: 'product-tags.css'
})
export class ProductTags {
    @Element() root: HTMLElement;

    @Prop({'mutable': true, 'reflect': true}) expanded: boolean = false;
    @Prop({'mutable': true, 'reflect': true}) expandable: number;

    componentWillLoad() {
        const count = product.get('tags').length;

        if(count > 6) this.expandable = 3
        else if(count > 4) this.expandable = 2
        else if(count > 3) this.expandable = 1
    }

	render() {
		return [
            <div>
                {product.get('tags').map(tag =>
                    <a href={tag.link}>{tag.name}</a>
                )}
            </div>,
            <ks-button transparent narrower round icon={this.expanded ? "minus" : "plus"}
                onClick={()=>this.expanded = !this.expanded}
            />
        ];
	}
}
