import { Component, State, Prop, h } from '@stencil/core';
import { productDynamic } from '../../../global/data/productDynamic';

@Component({
  tag: 'ks-product-admin',
  styleUrl: 'product-admin.css'
})
export class ProductAdmin {
    @State() expanded: boolean = false;
    @Prop({reflect: true, mutable: true}) enabled: boolean = false;

    render() {
        const admin = productDynamic.get("adminBar");

        this.enabled = !!productDynamic.get("loaded") && !!Object.keys(admin).length;
        if(!this.enabled) return null;
        
        const isSet = admin?.set?.length > 0;

        return [
            <div class="bar">
                {this.currentSettings(admin)}

                <div class="links">
                    <a class="button" href={admin.link}><ks-icon name="edit" aria-label="edit"></ks-icon></a>

                    {this.hasDistributors(admin) || isSet ? 
                        <button class="button" onClick={()=>this.expanded = !this.expanded} name='expand'>
                            <ks-icon name={this.expanded ? "chevron-up" : "chevron-down"}></ks-icon>
                        </button>
                    : null}
                </div>
            </div>,

            isSet ? 
                <div class="set" hidden={!this.expanded}>
                    {admin.set.map(item => [
                        <div class="bar">
                            <div class="name">{item.name}</div>
                            
                            {this.currentSettings(item)}

                            <div class="links">
                                <a class="button" href={item.link}><ks-icon name="edit"></ks-icon></a>
                            </div>
                        </div>,

                        this.distributors(item)
                    ])}
                </div>
            : null,

            this.distributors(admin)
        ];
    }

    hasDistributors(obj) {
        return obj?.distributors?.length > 0;
    }

    distributors(obj) {
        return this.hasDistributors(obj) ? 
            <div class="distributors" hidden={!this.expanded}>
                <tr>
                    <th>Dystrybutor</th>
                    <th>Czas wysyłki</th>
                    <th>Stan zewnętrzny</th>
                    <th>Tryb niedostępności</th>
                    <th>Komunikaty</th>
                </tr>
                {obj?.distributors?.map(distributor => 
                    <tr class={distributor.overwrite && "overwrite"}>
                        <td>{distributor.name}</td>
                        <td>{distributor.time}</td>
                        <td>{distributor.quantity}</td>
                        <td>{distributor.unavailableMode}</td>
                        <td>{distributor.warning}</td>
                    </tr>
                )}
            </div>
        : null
    }
    
    currentSettings(item) {
        return [
            <div><ks-icon name="home"></ks-icon> {item.homeQuantity}</div>,
            
            item.externalQuantity != "" ?
                <div><ks-icon name="truck"></ks-icon> {item.externalQuantity}</div>
            : null,

            item.availability != "" ?
                <div><ks-icon class="hide-mobile" name="x-octagon"></ks-icon> {item.availability}</div>
            : null,

            item.overwrite ?
                <div><ks-icon class="hide-mobile" name="alert-octagon"></ks-icon> Nadpisany</div>
            : null,
        ];
    }
}
