ks-product-info {
    display: block;
    position: relative;

    box-sizing: border-box;
    width: 100%;

    background: var(--card-background);
    color: var(--card-text-color);

    padding: 30px 30px;
    min-height: 420px;
    border-radius: 10px;
}

ks-product-info > * {
    margin: 0px auto;
    width: 100%;
    max-width: 700px;
}

ks-product-info .left {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

ks-product-info .buttons {
    display: flex;
    margin: 15px -5px 10px 0px;
}

ks-product-info .bottom {
    position: relative;
    padding-bottom: 60px;
}

@media only screen and (min-width: 961px) {
    ks-product-info {
        display: grid;
        grid-template-columns: 48% 52%;
        grid-template-rows: auto 1fr;
    }

    ks-product-info > .top {
        grid-column: 2;
        grid-row: 1;
    }
    ks-product-info > .left {
        grid-column: 1;
        grid-row: 1 / 3;
    }
    ks-product-info > .bottom {
        grid-column: 2;
        grid-row: 2;
        padding-bottom: 0px;
    }
}

@media only screen and (min-width: 1200px) {
    ks-product-info {
        padding: 30px 30px;
    }
}

@media only screen and (max-width: 960px) {
    ks-product-info .bottom {
        margin-top: 30px;

        display: flex;
        flex-direction: column;
        align-items: center;
    }

    ks-product-info .buttons {
        display: flex;
        margin: 15px 0px 10px 0px;
    }
}

@media only screen and (max-width: 460px) {
    ks-product-info {
        padding: 30px 10px;
    }
}