import { Component, h, Host, Listen, Prop } from '@stencil/core';

@Component({
	tag: 'ks-product-tooltip',
	styleUrl: 'product-tooltip.css'
})
export class ProductButton {
    @Prop() message: string;
    @Prop({mutable: true, reflect: true}) active: boolean = false;

    @Listen("mouseover") onMouseover() {
        this.active = true;
    }
    @Listen("mouseout") onMouseout() {
        this.active = false;
    }

    @Listen("focusin") onFocusIn() {
        this.active = true;
    }
    @Listen("focusout") onFocusOut() {
        this.active = false;
    }

	render() {
		return <Host>
            <slot/>
            <div class="arrow" />
            <div class="message">{this.message}</div>
        </Host>;
	}
}